  margin: 0;
}

.press-quotes__item:first-child {
  grid-column: 2/5 span;
}

.press-quotes__item:nth-child(2) {
  grid-column: 5 span;
}

.press-quotes__item:nth-child(3) {
  grid-column: 2/5 span;
}

.press-quotes__box {
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 0.5fr);
  column-gap: 15px;
}

.press-quotes__img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  box-shadow: 7px 0 0 var(--orange);
  grid-row: 2 span;
}

.press-quotes__name {
  font-size: 24px;
  line-height: 120%;
  color: var(--orange);
  grid-column: 3 span;
}

.press-quotes__works {
  font-family: Open-Sans;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  color: var(--white);
  opacity: 0.5;
  grid-column: 2/3 span;
  grid-row: 2/1 span;
}

.press-quotes__icon {
  grid-column: end;
  justify-self: start;
  align-self: start;
  font-size: 1rem;
  color: var(--white);
  opacity: 0.3;
}

.form__input-title,
.press-quotes__time {
  font-family: Open-Sans;
  font-weight: 400;
  font-size: 14px;
  color: var(--white);
  mix-blend-mode: normal;
  opacity: 0.5;
}

.press-quotes__quote {
  display: block;
  margin-bottom: 15px;
}

.press-quotes__time {
  display: block;
  line-height: 120%;
}

.subscribe {
  padding: 50px 0;
  background-color: #000;
  position: relative;
}

.subscribe__container {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  grid-template-areas: "img ." "img content" "img form" "img .";
}

.subscribe__img {
  grid-area: img;
}

.subscribe__img img {
  width: 100%;
  height: 100%;
}

.subscribe__content {
  max-width: 670px;
  grid-area: content;
}

.subscribe__text {
  margin-bottom: 70px;
}

.subscribe__form {
  grid-area: form;
}

.form {
  display: flex;
  gap: 15px;
}

.form__label-box {
  background: #363636;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  padding: 15px;
}

.form__input-title {
  margin-bottom: 10px;
}

.form__input {
  border: 0;
  color: var(--orange);
  background: #363636;
  max-width: 360px;
}

.form__input::placeholder {
  color: var(--orange);
}

.form__link {
  border: 0;
}

.swiper-slide {
  width: 100vw;
}

::-webkit-scrollbar {
  width: 0px;
  height: 5px;
  background: transparent;
  position: fixed;
}

.carousel__slides {
  transition: transform 0.3s ease;
}

.swiper-slide-visible {
  max-width: 480px;
  width: min(480px, max(35vw, 315px));
  transition-duration: 350ms;
}

.third .accordion-content::before {
  height: 0;
}

.third .accordion-box.accordion-box_active .accordion-content::before {
  height: 90%;
  transition: height 0.4s;
}

@media (min-width: 479.98px) {
  .form__input {
    min-width: 250px;
  }
}

@media (min-width: 768px) {
  .second__slider-cards {
    margin-right: 30px;
  }
}

@media (min-width: 1199.98px) {
  .third__accordion-btn {
    align-items: center;
  }
}

@media (max-width: 1440px) {
  .title {
    font-size: calc(33px + 47 * (100vw - 320px) / 1120);
  }

  .subtitle {
    font-size: calc(18px + 6 * (100vw - 320px) / 1120);
  }

  .text {
    font-size: calc(14px + 4 * (100vw - 320px) / 1120);
  }

  .second__slider-cards {
    width: calc(225px + 275 * (100vw - 320px) / 1120);
    height: calc(270px + 340 * (100vw - 320px) / 1120);
  }

  .third__accordion-btn {
    font-size: calc(21px + 15 * (100vw - 320px) / 1120);
  }
}

@media (max-width: 1199.98px) {
  .burger {
    display: block;
    z-index: 3;
  }

  .burger__line {
    position: absolute;
    right: 0;
    height: 3px;
    width: 100%;
    background-color: var(--white);
    border-radius: 3px;
    transition: width 0.3s;
  }

  .burger__line_top {
    top: 0;
  }

  .burger__line_middle {
    top: 50%;
    transform: translateY(-50%);
  }

  .burger__line_bottom {
    bottom: 0;
    width: 70%;
  }

  .burger.active .burger__line {
    width: 100%;
  }

  .menu-item {
    margin-right: 25px;
  }

  .header {
    padding: 10px 0;
  }

  .header.active::before {
    content: "";
    background-color: #000;
    position: absolute;
    z-index: 2;
    inset: 0;
  }

  .header__menu {
    margin: 0;
    position: fixed;
    top: -100%;
    left: 0;
    right: 0;
    bottom: 100%;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.75);
    overflow: auto;
    padding: 100px 30px 30px;
    transition: inset 0.3s;
  }

  .header__list {
    flex-direction: column;
    align-items: start;
    max-width: 320px;
    margin: 0 auto;
  }

  .header__item:not(:last-child) {
    margin: 0 0 30px;
  }

  .header__link {
    font-size: 25px;
    white-space: normal;
  }

  .header__icons-box {
    margin-top: 30px;
  }

  .header__link-icon {
    margin-right: 30px;
  }

  .header__link-icon svg {
    width: 40px;
    height: 40px;
  }

  .fadeIn--open .header__menu {
    top: 0;
  }
}

@media (max-width: 1124px) {
  .header__item:not(:last-child) {
    margin-right: 25px;
  }
}

@media (max-width: 991.98px) {
  .logo {
    flex: 0 0 33px;
  }

  .scroll-snap-section {
    scroll-snap-align: none;
  }

  .main {
    height: 100%;
    scroll-snap-type: none;
  }

  .footer__bottom {
    flex-direction: column;
    align-items: center;
  }

  .first__subtitle {
    margin-bottom: 15px;
  }

  .first .swiper-button-next::before,
  .first .swiper-button-prev::before {
    height: 2px;
  }

  .first .swiper-button-next::after,
  .first .swiper-button-prev::after {
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
  }

  .first .swiper-button-next,
  .first .swiper-button-prev {
    width: 30px;
  }

  .first .swiper-button-next::after {
    border-left: 5px solid var(--white);
  }

  .first .swiper-button-prev::after {
    border-right: 5px solid var(--white);
  }
}

@media (max-width: 767.98px) {
  .footer .logo {
    width: 50px;
    height: 50px;
    margin-bottom: 30px;
  }

  .footer__menu {
    flex-direction: column;
  }

  .footer__list {
    justify-content: center;
    min-width: 270px;
    margin: 0 0 30px;
  }

  .footer__privacy-policy {
    flex-direction: column;
    align-items: center;
  }

  .second__container {
    flex-direction: column;
    gap: 20px;
  }

  .second__slider-cards .swiper-pagination {
    gap: 20px;
  }

  .second__slider-cards .swiper-button-prev {
    left: -35px;
  }

  .second__slider-cards .swiper-button-next {
    right: -35px;
  }

  .quotes__container {
    grid-template-columns: 1fr;
    row-gap: 30px;
  }

  .press-quotes {
    grid-template-areas: "a a a a a a" "b b b b b b" "c c c c c c";
  }

  .subscribe__container {
    grid-template-areas: "img content" "form form";
    align-items: stretch;
  }

  .subscribe__form {
    justify-content: end;
  }
}

@media (max-width: 767px) {
  .swiper-slide-visible {
    width: min(315px, max(41.0691003911vw, 100px));
  }
}

@media (max-width: 576.98px) {
  .press-quotes__item {
    padding: 15px;
  }

  .press-quotes__box {
    column-gap: 15px;
  }

  .press-quotes__img {
    width: 2.5rem;
    height: 2.5rem;
  }

  .press-quotes__name {
    font-size: 21px;
  }

  .press-quotes__works {
    font-size: 14px;
  }
}

@media (max-width: 479.98px) {
  .footer__copyright {
    font-size: 12px;
  }

  .footer__privacy-policy {
    max-width: 170px;
    text-align: center;
  }

  .first__title {
    margin-bottom: 5px;
  }

  .form__link,
  .quotes__link {
    max-width: 100%;
  }

  .subscribe__img {
    position: absolute;
    left: -100px;
    height: 100%;
  }

  .subscribe__form {
    flex-direction: column;
  }

  .press-quotes__item:first-child {
    grid-column: 6 span;
  }

  .press-quotes__item:nth-child(2) {
    grid-column: 6 span;
  }

  .press-quotes__item:nth-child(3) {
    grid-column: 6 span;
  }
}

@media (pointer: fine) and (hover: hover) {
  .second__slider-cards .swiper-button-next:hover svg,
  .second__slider-cards .swiper-button-prev:hover svg,
  .socials__icon:hover {
    stroke: var(--orange);
  }

  .link:hover {
    background-color: var(--white);
  }

  .menu-link {
    transition: color 0.3s;
  }

  .header__lang-btn:hover,
  .menu-link:hover {
    color: var(--orange);
  }

  .socials__icon:hover {
    fill: var(--orange);
  }

  .swiper-pagination-bullet:hover {
    border-color: var(--orange);
  }

  .swiper-pagination-bullet-active:hover {
    background-color: var(--orange);
  }

  .header__lang-select:hover .header__list-lang {
    visibility: visible;
    pointer-events: all;
    opacity: 1;
  }

  .header__lang-select:hover .header__lang-btn_selected::after {
    background: url(./assets/img/sprite.svg#arrow-down_orange) center/cover no-repeat;
    transform: translateY(-50%) rotate(180deg);
  }

  .header__link-icon:hover svg {
    fill: var(--white);
  }

  .first .swiper-button-next,
  .first .swiper-button-prev,
  .first .swiper-pagination {
    display: none;
  }

  .press-quotes__icon:hover {
    color: var(--orange);
    opacity: 1;
  }
}

@media (pointer: coarse) {
  .scroll-snap-section {
    scroll-snap-align: none;
  }

  .header__lang-select.open .header__list-lang {
    visibility: visible;
    pointer-events: all;
    opacity: 1;
  }

  .header__lang-select.open .header__lang-btn_selected {
    color: var(--orange);
  }

  .header__lang-select.open .header__lang-btn_selected::after {
    background: url(./assets/img/sprite.svg#arrow-down_orange) center/cover no-repeat;
    transform: translateY(-50%) rotate(180deg);
  }

  .main {
    scroll-snap-type: none;
    height: 100%;
  }

  .first .swiper-button-next,
  .first .swiper-button-prev,
  .first .swiper-pagination {
    display: flex;
  }
}

@media (hover: hover) {
  .first__arrow-down:hover {
    color: var(--orange);
  }
}