.swiper-slide,
.swiper-wrapper,
swiper-slide {
    height: 100%;
    position: relative;
    transition-property: transform;
    width: 100%
}

.subtitle,
.title {
    font-family: Bebas-Neue;
    font-weight: 700;
    color: var(--white)
}

.subtitle,
.text,
.title {
    color: var(--white)
}

.menu-link,
.title {
    text-transform: uppercase
}

.footer__privacy-policy span,
.header__link,
.visually-hidden {
    white-space: nowrap
}

.first #select,
.link {
    background-color: var(--orange)
}

.first__container,
.four__content,
.quotes__link {
    text-align: center
}

:root {
    --orange: #FFB548;
    --white: #fff;
    --swiper-theme-color: #007aff
}

html {
    scroll-behavior: smooth;
    box-sizing: border-box
}

.table__descr,
body {
    margin: 0
}

body.lock-scroll {
    overflow: hidden;
    position: fixed;
    left: 0;
    height: 100vh;
    width: 100vw
}

body,
html {
    height: 100%;
    width: 100%
}

.wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    min-height: 100%;
    overflow: hidden
}

.first__container,
.swiper-vertical>.swiper-wrapper {
    flex-direction: column
}

