.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden
}

.burger,
.first,
.header__lang-select,
.ibg,
.logo,
.third .accordion-box {
    position: relative
}

.title {
    margin: 0;
    font-size: 80px;
    line-height: normal;
    letter-spacing: .04rem
}

.subtitle {
    font-size: 24px;
    line-height: 130%;
    letter-spacing: .15rem
}

.link,
.text {
    font-family: Open-Sans
}

.menu-link,
.text {
    line-height: 120%
}

.text {
    font-size: 18px;
    font-weight: 400;
    letter-spacing: .04rem
}

.link {
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    color: #1a1917;
    transition: background-color .3s;
    border-radius: 4px;
    padding: 15px 20px;
    display: block;
    max-width: fit-content
}

.burger,
.header__lang-btn.select__option_hidden {
    display: none
}

.btn-reset {
    background: rgba(0, 0, 0, 0);
    cursor: pointer;
    border: 0;
    padding: 0
}

.burger {
    width: 35px;
    height: 30px
}

.logo {
    flex: 0 0 55px;
    z-index: 3;
    transition: flex .3s
}

.logo img {
    width: 100%;
    height: 100%
}

.header,
.ibg img {
    position: absolute;
    top: 0;
    left: 0
}

.ibg img {
    width: 100%;
    height: 100%;
    object-fit: cover
}

.flex-all-center {
    display: flex;
    align-items: center;
    justify-content: center
}

.flex-v-center {
    display: flex;
    align-items: center
}

.bg {
    background-position: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh
}

.scroll-snap-section {
    scroll-snap-align: start
}

.menu-item {
    transition: margin .3s;
    margin-right: 40px
}

.menu-item:last-child {
    margin-right: 0
}

.menu-link {
    font-weight: 700;
    font-size: inherit;
    color: inherit
}

.footer__privacy-policy,
.header__lang-btn {
    color: var(--white);
    text-transform: uppercase
}

.socials {
    padding: 0
}

.socials__item:not(:last-child) {
    margin-right: 40px
}

.socials__icon {
    fill: var(--white);
    transition: fill .3s
}

.socials__icon_facebook {
    width: 10px;
    height: 20px
}

.socials__icon_twitter {
    width: 21px;
    height: 16px
}

.socials__icon_youtube {
    width: 25px;
    height: 18px;
    fill: #ff1313
}

.socials__icon_twitch {
    stroke: #fff;
    width: 19px;
    height: 18px
}

.swiper-button-disabled {
    transition: opacity .3s;
    opacity: 0;
    pointer-events: none
}

.swiper-pagination {
    display: flex;
    gap: 12px
}

.swiper-pagination-bullet {
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid var(--white);
    cursor: pointer
}

.swiper-pagination-bullet-active {
    background-color: var(--white)
}

