.header {
    padding: 22px 0;
    z-index: 2;
    width: 100%;
    transition: padding .3s
}

.header.active .header__menu {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block
}

.header.active .header__list {
    display: flex
}

.header.active .header__menu-right {
    position: relative;
    z-index: 2
}

.header__container {
    justify-content: space-between
}

.header__menu {
    padding: 0;
    margin: 0 30px
}

.header__list {
    padding: 0;
    list-style: none
}

.header__item:not(:last-child) {
    margin-right: 35px
}

.header__lang-btn {
    font-size: 30px;
    transition: color .3s
}

.header__item-lang:not(:last-child) {
    margin-bottom: 5px
}

.header__lang-btn_selected {
    padding-right: 25px;
    position: relative
}

.header__lang-btn_selected::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 20px;
    height: 15px;
    background: url(./assets/img/sprite.svg#arrow-down) center/cover no-repeat;
    transform: translateY(-50%);
    transition: transform .3s, background .3s
}

.header__lang-box {
    border-right: solid 1px var(--white);
    padding-right: 15px;
    margin-right: 15px;
    display: flex
}

.header__list-lang {
    margin: 0;
    list-style: none;
    position: absolute;
    width: 100%;
    top: 100%;
    left: -5px;
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    background: rgba(0, 0, 0, .9);
    border-radius: 3px;
    padding: 5px;
    transition: opacity .3s, visibility .3s
}

.header__link-icon {
    margin: 0 10px 0 0;
    width: 33px;
    height: 33px;
    position: relative
}

.header__link-icon svg {
    width: 30px;
    height: 30px;
    transition: fill .3s;
    fill: var(--orange)
}

.header__link-icon:last-child {
    margin: 0;
    border: none
}

.main {
    flex: 1 1 auto;
    position: relative;
    overflow-x: hidden;
    min-height: 650px;
    height: 100vh
}

.main.lock-scroll-snap {
    height: 100%;
    scroll-snap-type: none
}

.main.lock-scroll-snap .scroll-snap-section {
    scroll-snap-align: none
}

.footer {
    padding: 30px 0px 20px 0px;
    background-color: #000;
    transition: padding .3s
}

.footer .logo {
    margin-right: 25px
}

.footer__menu {
    padding-bottom: 30px;
    margin-bottom: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, .6039215686);
    justify-content: space-between
}

.footer__list {
    padding: 0;
    list-style: none;
    display: inline-flex;
    flex-wrap: wrap;
    margin-right: 40px;
    gap: 20px
}

.footer__bottom {
    display: flex;
    justify-content: space-between;
    gap: 15px;
}

.footer__copyright {
    font-family: Open-Sans;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    color: var(--white);
    mix-blend-mode: normal;
    opacity: .5
}

.footer__privacy-policy {
    margin: 0;
    font-family: Open-Sans;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: .1em;
    display: flex;
    gap: 5px
}

.press-quotes__name,
.third__accordion-btn {
    font-family: Bebas-Neue;
    font-weight: 700;
    letter-spacing: 2px
}

.first{
    padding: 70px 0px 50px 0px;
}

.first__container {
    position: relative;
    z-index: 1;
    display: flex;
    margin-bottom: 50px
}

.first__subtitle {
    margin-bottom: 30px
}

.first__link {
    align-self: center
}

.first__arrow-down {
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    z-index: 2;
    display: inline-flex;
    flex-direction: column;
    align-items: center
}

.first__arrow-down::after {
    content: "";
    border: 10px solid transparent;
    border-top: 10px solid currentColor;
    border-bottom: none;
    margin-top: 5px
}

.first .swiper-slide img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1
}

.first .swiper-first {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.first .swiper-wrapper {
    pointer-events: none
}

.first .swiper-button-next,
.first .swiper-button-prev {
    width: 40px;
    height: 25px;
    display: inline-flex;
    align-items: center
}

.first .swiper-button-next::before,
.first .swiper-button-prev::before {
    content: "";
    height: 3px;
    width: 100%;
    background-color: var(--white)
}

.first .swiper-button-next::after,
.first .swiper-button-prev::after {
    content: "";
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent
}

.first .swiper-button-next::after {
    border-left: 10px solid var(--white)
}

.first .swiper-button-prev {
    flex-direction: row-reverse
}

.first .swiper-button-prev::after {
    border-right: 10px solid var(--white)
}

.first .swiper-button-disabled {
    opacity: 0
}

.first .box {
    display: flex;
    justify-content: space-evenly;
    position: relative;
    z-index: 1;
    bottom: 20%;
    align-items: center
}

.first #select {
    padding: 15px
}

.first #select option {
    background-color: #00f;
    color: brown
}

.second {
    background-image: url(./assets/img/bg-second.jpg);
    padding: 20px 0
}

.second__container {
    display: flex;
    align-items: center;
    gap: 40px;
    margin: auto
}

.second__title {
    border-bottom: 3px solid var(--white);
    margin-right: 60px
}

.second__text {
    margin-top: 1.5rem;
    max-width: 100%
}

.second__slider-cards {
    width: 500px;
    height: 610px;
    padding: 0 10px
}

.second__slider-cards .swiper-pagination {
    gap: 50px;
    justify-content: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10%;
    z-index: 2
}

.second__slider-cards .swiper-button-next,
.second__slider-cards .swiper-button-prev {
    position: absolute;
    top: 50%;
    z-index: 2
}

.second__slider-cards .swiper-button-next svg,
.second__slider-cards .swiper-button-prev svg {
    width: 25px;
    height: 45px;
    stroke: var(--white);
    stroke-width: 3px
}

.second__slider-cards .swiper-button-prev {
    transform: rotate(180deg) translateY(50%);
    left: -30px
}

.second__slider-cards .swiper-button-next {
    right: -30px;
    transform: translateY(-50%)
}

.second__slider-cards .swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover
}

.third {
    background-image: url(./assets/img/bg-third.jpg);
    padding: 50px 0px;
}

.third__container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%
}

.third__content {
    margin-left: 30px
}

.third__accordion {
    max-width: 600px
}

.third .accordion-box.accordion-box_active .accordion-content::before {
    content: "";
    border: 1px solid var(--white);
    background-color: var(--white)
}

.third__accordion-btn {
    position: relative;
    display: flex;
    width: 100%;
    font-size: 36px;
    line-height: 130%;
    color: var(--orange);
    margin: .7rem 0 .3rem;
    gap: 35px
}

.third__accordion-btn::before {
    content: "";
    border: .2rem solid var(--white);
    border-radius: 50%;
    width: 14px;
    height: 14px;
    position: absolute;
    left: -24px;
    top: 11px;
    overflow: hidden
}

.third__accordion-btn.accordion-btn_active::before {
    background-color: var(--white)
}

.third__accordion-btn.accordion-btn_active+.accordion-content {
    max-height: 100vh
}

.third .accordion-content {
    transition: max-height .3s;
    padding: 0 20px;
    overflow: hidden;
    max-height: 0
}

.third .accordion-content::before {
    content: "";
    width: 2px;
    height: 90%;
    position: absolute;
    border-radius: 10px;
    left: -18px;
    top: 17px;
    overflow: hidden
}

.four {
    background-image: url(./assets/img/bg-four.jpg);
    padding: 50px 0
}

.four__container {
    width: 100%;
}

.four__content{
    margin-bottom: 30px;
}



.four__title,
.quotes__title,
.subscribe__title {
    margin-bottom: 45px
}

.table {
    max-width: 575px;
    color: #fff;
    font-size: 30px;
    display: grid;
    background-color: #00000075;
    border-radius: 5px;
    grid-template-columns: repeat(6, 1fr);
}

.table__item {
    padding: .7rem .7rem 1.4rem;
    border: 1px solid rgba(151, 151, 151, .5137254902)
}

.table__item:nth-child(-n+4) {
    grid-column: 3 span;
}

.table__item:last-child {
    grid-column: 6 span;
}

.table__title {
    color: var(--orange);
    margin-bottom: 10px
}

.quotes {
    background: radial-gradient(78.66% 177.87% at 19.56% 37.57%, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0) 19.22%, #080A0B 99.74%),url(./assets/img/bg-five.jpg);
    background-repeat: repeat;
    background-size: cover;
    background-position: center;
    padding: 50px 0
}

.quotes__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    margin: auto;
    column-gap: 30px
}

.quotes__content {
    max-width: 400px
}

.quotes__text {
    margin-bottom: 40px
}

.press-quotes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 30px;
}

.press-quotes__item {
    background: rgba(255, 255, 255, .08);
    border-radius: 10px;
    padding: 15px 20px 20px 15px;
    margin: 0
}

.press-quotes__item:first-child {
    grid-column: 2 / 5 span;
}

.press-quotes__item:nth-child(2) {
    grid-column: 5 span;
}

.press-quotes__item:nth-child(3) {
    grid-column: 2 / 5 span;
}

.press-quotes__box {
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 0.5fr);
    column-gap: 15px;
}

.press-quotes__img {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    box-shadow: 7px 0 0 var(--orange);
    grid-row: 2 span;
}

.press-quotes__name {
    font-size: 24px;
    line-height: 120%;
    color: var(--orange);
    grid-column: 3 span;
}

.press-quotes__works {
    font-family: Open-Sans;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--white);
    opacity: .5;
    grid-column: 2 / 3 span;
    grid-row: 2 / 1 span;
}

.press-quotes__icon {
    grid-column: end;
    justify-self: start;
    align-self: start;
    font-size: 1rem;
    color: var(--white);
    opacity: .3;
}

.form__input-title,
.press-quotes__time {
    font-family: Open-Sans;
    font-weight: 400;
    font-size: 14px;
    color: var(--white);
    mix-blend-mode: normal;
    opacity: .5
}

.press-quotes__quote {
    display: block;
    margin-bottom: 15px
}

.press-quotes__time {
    display: block;
    line-height: 120%
}

.subscribe {
    padding: 50px 0;
    background-color: #000;
    position: relative
}

.subscribe__container {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    grid-template-areas: "img ." "img content" "img form" "img ."
}

.subscribe__img {
    grid-area: img
}

.subscribe__img img {
    width: 100%;
    height: 100%
}

.subscribe__content {
    max-width: 670px;
    grid-area: content
}

.subscribe__text {
    margin-bottom: 70px
}

.subscribe__form {
    grid-area: form
}

.form {
    display: flex;
    gap: 15px
}

.form__label-box {
    background: #363636;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    padding: 15px
}

.form__input-title {
    margin-bottom: 10px
}

.form__input {
    border: 0;
    color: var(--orange);
    background: #363636;
    max-width: 360px
}

.form__input::placeholder {
    color: var(--orange)
}

.form__link {
    border: 0
}

@media (min-width:479.98px) {
    .form__input {
        min-width: 250px
    }
}

@media (min-width:768px) {
    .second__slider-cards {
        margin-right: 30px
    }
}

@media (min-width:1199.98px) {
    .third__accordion-btn {
        align-items: center
    }
}

@media (max-width:1440px) {
    .title {
        font-size: calc(33px + 47 * ((100vw - 320px) / 1120))
    }

    .subtitle {
        font-size: calc(18px + 6 * ((100vw - 320px) / 1120))
    }

    .text {
        font-size: calc(14px + 4 * ((100vw - 320px) / 1120))
    }

    .second__slider-cards {
        width: calc(225px + 275 * ((100vw - 320px)/ 1120));
        height: calc(270px + 340 * ((100vw - 320px)/ 1120))
    }

    .third__accordion-btn {
        font-size: calc(21px + 15 * ((100vw - 320px) / 1120))
    }
}

@media (max-width:1199.98px) {
    .burger {
        display: block;
        z-index: 3
    }

    .burger__line {
        position: absolute;
        right: 0;
        height: 3px;
        width: 100%;
        background-color: var(--white);
        border-radius: 3px;
        transition: width .3s
    }

    .burger__line_top {
        top: 0
    }

    .burger__line_middle {
        top: 50%;
        transform: translateY(-50%)
    }

    .burger__line_bottom {
        bottom: 0;
        width: 70%
    }

    .burger.active .burger__line {
        width: 100%
    }

    .menu-item {
        margin-right: 25px
    }

    .header {
        padding: 10px 0
    }

    .header.active::before {
        content: "";
        background-color: #000;
        position: absolute;
        z-index: 2;
        inset: 0
    }

    .header__menu {
        margin: 0;
        position: fixed;
        top: -100%;
        left: 0;
        right: 0;
        bottom: 100%;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .75);
        overflow: auto;
        padding: 100px 30px 30px;
        transition: inset .3s
    }

    .header__list {
        flex-direction: column;
        align-items: start;
        max-width: 320px;
        margin: 0 auto;
        // display: none
    }

    .header__item:not(:last-child) {
        margin: 0 0 30px
    }

    .header__link {
        font-size: 25px;
        white-space: normal
    }

    .header__icons-box {
        margin-top: 30px
    }

    .header__link-icon {
        margin-right: 30px
    }

    .header__link-icon svg {
        width: 40px;
        height: 40px;
    }
}

@media (max-width:1124px) {
    .header__item:not(:last-child) {
        margin-right: 25px
    }
}

@media (max-width:991.98px) {
    .logo {
        flex: 0 0 33px
    }

    .scroll-snap-section {
        scroll-snap-align: none
    }

    .main {
        height: 100%;
        scroll-snap-type: none
    }

    .footer__bottom {
        flex-direction: column;
        align-items: center
    }

    .first__subtitle {
        margin-bottom: 15px
    }

    .first .swiper-button-next::before,
    .first .swiper-button-prev::before {
        height: 2px
    }

    .first .swiper-button-next::after,
    .first .swiper-button-prev::after {
        border-top: 5px solid transparent;
        border-bottom: 5px solid transparent
    }

    .first .swiper-button-next,
    .first .swiper-button-prev {
        width: 30px
    }

    .first .swiper-button-next::after {
        border-left: 5px solid var(--white)
    }

    .first .swiper-button-prev::after {
        border-right: 5px solid var(--white)
    }

}

@media (max-width:767.98px) {
    .footer .logo {
        width: 50px;
        height: 50px;
        margin-bottom: 30px
    }

    .footer__menu {
        flex-direction: column
    }

    .footer__list {
        justify-content: center;
        min-width: 270px;
        margin: 0 0 30px
    }

    .footer__privacy-policy {
        flex-direction: column;
        align-items: center
    }

    .second__container {
        flex-direction: column;
        gap: 20px
    }

    .second__slider-cards .swiper-pagination {
        gap: 20px
    }

    .second__slider-cards .swiper-button-prev {
        left: -35px
    }

    .second__slider-cards .swiper-button-next {
        right: -35px
    }

    .quotes__container {
        grid-template-columns: 1fr;
        row-gap: 30px
    }

    .press-quotes {
        grid-template-areas: "a a a a a a" "b b b b b b" "c c c c c c"
    }

    .subscribe__container {
        grid-template-areas: "img content" "form form";
        align-items: stretch
    }

    .subscribe__form {
        justify-content: end
    }
}

@media (max-width:576.98px) {
    .press-quotes__item {
        padding: 15px
    }

    .press-quotes__box {
        column-gap: 15px
    }

    .press-quotes__img {
        width: 2.5rem;
        height: 2.5rem
    }

    .press-quotes__name {
        font-size: 21px
    }

    .press-quotes__works {
        font-size: 14px
    }
}

@media (max-width:479.98px) {
    .footer__copyright {
        font-size: 12px
    }

    .footer__privacy-policy {
        max-width: 170px;
        text-align: center
    }

    .first__title {
        margin-bottom: 5px
    }

    .form__link,
    .quotes__link {
        max-width: 100%
    }

    .subscribe__img {
        position: absolute;
        left: -100px;
        height: 100%
    }

    .subscribe__form {
        flex-direction: column
    }

    .press-quotes__item:first-child {
        grid-column: 6 span;
    }
    
    .press-quotes__item:nth-child(2) {
        grid-column: 6 span;
    }
    
    .press-quotes__item:nth-child(3) {
        grid-column: 6 span;
    }
}

@media (pointer:fine)and (hover:hover) {

    .second__slider-cards .swiper-button-next:hover svg,
    .second__slider-cards .swiper-button-prev:hover svg,
    .socials__icon:hover {
        stroke: var(--orange)
    }

    .link:hover {
        background-color: var(--white)
    }

    .menu-link {
        transition: color .3s
    }

    .header__lang-btn:hover,
    .menu-link:hover {
        color: var(--orange)
    }

    .socials__icon:hover {
        fill: var(--orange)
    }

    .swiper-pagination-bullet:hover {
        border-color: var(--orange)
    }

    .swiper-pagination-bullet-active:hover {
        background-color: var(--orange)
    }

    .header__lang-select:hover .header__list-lang {
        visibility: visible;
        pointer-events: all;
        opacity: 1
    }

    .header__lang-select:hover .header__lang-btn_selected::after {
        background: url(./assets/img/sprite.svg#arrow-down_orange) center/cover no-repeat;
        transform: translateY(-50%) rotate(180deg)
    }

    .header__link-icon:hover svg {
        fill: var(--white)
    }

    .first .swiper-button-next,
    .first .swiper-button-prev,
    .first .swiper-pagination {
        display: none
    }

    .press-quotes__icon:hover {
        color: var(--orange);
        opacity: 1
    }
}

@media (pointer:coarse) {
    .scroll-snap-section {
        scroll-snap-align: none
    }

    .header__lang-select.open .header__list-lang {
        visibility: visible;
        pointer-events: all;
        opacity: 1
    }

    .header__lang-select.open .header__lang-btn_selected {
        color: var(--orange)
    }

    .header__lang-select.open .header__lang-btn_selected::after {
        background: url(./assets/img/sprite.svg#arrow-down_orange) center/cover no-repeat;
        transform: translateY(-50%) rotate(180deg)
    }

    .main {
        scroll-snap-type: none;
        height: 100%
    }

    .first .swiper-button-next,
    .first .swiper-button-prev,
    .first .swiper-pagination {
        display: flex
    }
}

@media (hover:hover) {
    .first__arrow-down:hover {
        color: var(--orange)
    }
}